<template>
  <div>
    <div class="card-image">
      <figure class="image is-4by3">
        <img alt="Placeholder image" :src="getProductImage.icon" />
      </figure>
    </div>
    <div class="card-content">
      <div class="media">
        <div class="media-content">
          <p class="title is-4">{{ product.title }}</p>
        </div>
        <div>
            <button
              class="button is-small"
              :title="removeFromFavouriteLabel"
              v-show="product.isFavourite"
              @click="removeFromFavourite(product.id)"
            >
              <span class="icon is-small" style="color: red">
                <i class="fa fa-heart"></i>
              </span>
            </button>
            <button
              class="button is-small"
              :title="addToFavouriteLabel"
              v-show="!product.isFavourite"
              @click="saveToFavorite(product.id)"
            >
              <span class="icon is-small" style="color: pink">
                <i class="fa fa-heart"></i>
              </span>
            </button>
        </div>
      </div>
      <div class="content is-clearfix">
        <p>{{ product.description }}</p>
        <div class="is-pulled-left">
                    <i v-if="product.ratings === 1" class="fa fa-star"></i>
          <i v-if="product.ratings === 2" class="fa fa-star"></i>
          <i v-if="product.ratings === 2" class="fa fa-star"></i>
          <i v-if="product.ratings === 3" class="fa fa-star"></i>
          <i v-if="product.ratings === 3" class="fa fa-star"></i>
          <i v-if="product.ratings === 3" class="fa fa-star"></i>
          <i v-if="product.ratings === 4" class="fa fa-star"></i>
          <i v-if="product.ratings === 4" class="fa fa-star"></i>
          <i v-if="product.ratings === 4" class="fa fa-star"></i>
          <i v-if="product.ratings === 4" class="fa fa-star"></i>
          <i v-if="product.ratings === 5" class="fa fa-star"></i>
          <i v-if="product.ratings === 5" class="fa fa-star"></i>
          <i v-if="product.ratings === 5" class="fa fa-star"></i>
          <i v-if="product.ratings === 5" class="fa fa-star"></i>
          <i v-if="product.ratings === 5" class="fa fa-star"></i>
          <p>
            {{
              product.reviews > 0 ? `${product.reviews} Reviews` : "No reviews"
            }}
          </p>
        </div>
        <p class="is-pulled-right">
          <!-- <span class="title is-4"><strong>{{ product.price }}</strong></span> -->
          <span class="title is-4">
            <strong>{{ formatPrice }}</strong>
          </span>
        </p>
      </div>
      <div class="card-footer btn-actions">
        <div class="card-footer-item field is-grouped">
          <div class="buttons">
            <button class="button is-primary" v-if="!product.isAddedToCart" @click="addToCart(product.id)">
              {{ addToCartLabel }}
            </button>
            <button class="button is-text" v-if="product.isAddedToCart" @click="removeProductFromCart(product.id, false)">
              {{ removeFromCartLabel }}
            </button>
          </div>
          <div class="select is-rounded is-small">
            <select @change="onSelectQuantity(product.id)" v-model="selected">
              <option
                v-for="quantity in quantityArray"
                :value="quantity"
                :key="quantity.id"
              >
                {{ quantity }}
              </option>
            </select>
          </div>
        </div>
      </div>
    </div>
    <router-link
      class="details"
      :to="{
        name: 'detail-product',
        path: `/detail-product/${product.id}`,
        params: {
          id: product.id,
        },
      }"
    ></router-link>
    <router-view />
  </div>
</template>

<script>
import {mapState, mapGetters, mapMutations, mapActions} from 'vuex'

export default {
  name: "products",
  props: ["product"],
  data() {
    return {
      addToCartLabel: "Add to cart",
      viewDetailsLabel: "Details",
      removeFromCartLabel: "Remove from cart",
      addToFavouriteLabel: "Add to favourite",
      removeFromFavouriteLabel: "Remove from favourite",
      selected: 1,
      quantityArray: [],
    };
  },
  mounted() {
    this.getQuantity()
  },
  computed: {
    ...mapState(['userInfo']),

    ...mapGetters(['isUserLoggedIn']),

    getProductImage() {
      return {
        icon:
          this.product.image && require(`@/assets/images/${this.product.image}.jpg`),
      };
    },

    formatPrice() {
      if (this?.product?.price) {
        return new Intl.NumberFormat("en-US", {
          style: "currency",
          currency: "VND",
        }).format(this.product.price);
      } else {
        return "vnd";
      }
    },

    isUserLogged() {
      return this.isUserLoggedIn;
    },
  },
  methods: {
    ...mapMutations(['removeFromFavourite','removeFromCart']),

    ...mapActions([
    'changeQuantity',
    'addProductToCart',
    'changeAddedBtn',
    'showLoginModal',
    'addToFavourite'
    ]),

    getQuantity() {
      for (let i = 1; i <= 20; i++) {
      this.quantityArray.push(i);
      }
      if (this.$props.product.quantity > 1) {
        this.selected = this.$props.product.quantity;
      }
    },

    addToCart(id) {
      let data = {
        id: id,
        status: true,
      };
      this.addProductToCart(id);
      this.changeAddedBtn(data);
    },
    removeProductFromCart(id) {
      let data = {
        id: id,
        status: false,
      };
      this.removeFromCart(id);
      this.changeAddedBtn(data);
    },
    saveToFavorite(id) {
      let isUserLogged = this.userInfo.isLoggedIn;
      if (isUserLogged) {
        this.addToFavourite(id);
      } else {
        this.showLoginModal(true);
      }
    },
   
    onSelectQuantity(id) {
      let data = {
        id: id,
        quantity: this.selected,
      };
      this.changeQuantity(data);
    },
  },
};
</script>
<style scoped>
.details {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.details:hover {
  border: 1px solid #51bafc;
}
.button,
.select {
  z-index: 2;
}
.select {
  position: absolute;
  right: 15px;
  bottom: 35px;
}
.card-content {
  padding: 0;
}
.buttons {
  margin: 0;
}
</style>
